/* The checkbox_input */
.checkbox_input {
  display: block;
  position: relative;
  padding-left: 25px;
  margin-bottom: 6px;
  cursor: pointer;
  user-select: none;
  /* Hide the browser's default checkbox */
  /* On mouse-over, add a grey background color */
  /* When the checkbox is checked, add a blue background */
  /* Style the checkmark/indicator */
  /* Create a custom checkbox */ }
  .checkbox_input input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
    /* Show the checkmark when checked */ }
    .checkbox_input input:checked ~ .checkmark:after {
      display: block; }
  .checkbox_input .checkmark:after {
    left: 4px;
    top: 0px;
    width: 5px;
    height: 10px;
    border: solid #212b36;
    border-width: 0 3px 3px 0;
    transform: rotate(45deg); }
  .checkbox_input .checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 16px;
    width: 16px;
    border: 1px solid #919eab;
    /* Create the checkmark/indicator (hidden when not checked) */ }
    .checkbox_input .checkmark:after {
      content: "";
      position: absolute;
      display: none; }
    .checkbox_input .checkmark.with_color {
      border-radius: 5px; }
    .checkbox_input .checkmark.is_red {
      background-color: #de3618; }
    .checkbox_input .checkmark.is_green {
      background-color: #50b83c; }
    .checkbox_input .checkmark.is_yellow {
      background-color: #eec200; }
    .checkbox_input .checkmark.is_blue {
      background-color: #5c6ac4; }
    .checkbox_input .checkmark.is_cyan {
      background-color: #47c1bf; }
    .checkbox_input .checkmark.is_grey {
      background-color: #dfe3e8; }

